import scoreUtils from '../utils/score';

const scoreBoard = {
  level: 1,
  score: 0,
  best: scoreUtils.get(),
};

let canvas;
let ctx;
let speed = 4;
let paddleWidth = 80;
let out = false;

const brickRowCount = 9;
const brickColumnCount = 5;
const delay = 500; //delay to reset the game

// Create ball props
const ball = {
  size: 10,
  speed,
  dx: 4,
  dy: -4,
  visible: true,
};

// Create paddle props
const paddle = {
  w: paddleWidth,
  h: 10,
  speed: 8,
  dx: 0,
  visible: true,
};

// Create brick props
const brickInfo = {
  w: 70,
  h: 20,
  padding: 10,
  offsetX: 45,
  offsetY: 60,
  visible: true,
};

// Create bricks
const bricks = [];
function createBricks() {
  for (let i = 0; i < brickRowCount; i++) {
    bricks[i] = [];
    for (let j = 0; j < brickColumnCount; j++) {
      const x = i * (brickInfo.w + brickInfo.padding) + brickInfo.offsetX;
      const y = j * (brickInfo.h + brickInfo.padding) + brickInfo.offsetY;
      bricks[i][j] = { x, y, ...brickInfo };
    }
  }
}
createBricks();

// Draw ball on canvas
function drawBall(color = '#0095dd') {
  ctx.beginPath();
  ctx.arc(ball.x, ball.y, ball.size, 0, Math.PI * 2);
  ctx.fillStyle = ball.visible ? color : 'transparent';
  ctx.fill();
  ctx.closePath();
}

// Draw paddle on canvas
function drawPaddle() {
  ctx.beginPath();
  ctx.rect(paddle.x, paddle.y, paddle.w, paddle.h);
  ctx.fillStyle = paddle.visible ? '#0095dd' : 'transparent';
  ctx.fill();
  ctx.closePath();
}

// Draw score on canvas
function drawScore() {
  ctx.font = '20px Arial';
  scoreBoard.best =
    scoreBoard.score > scoreBoard.best ? scoreBoard.score : scoreBoard.best;
  ctx.fillText(`Level: ${scoreBoard.level}`, canvas.width - 700, 30);
  ctx.fillText(`Score: ${scoreBoard.score}`, canvas.width - 500, 30);
  ctx.fillText(`Best: ${scoreBoard.best}`, canvas.width - 300, 30);
  scoreUtils.update(scoreBoard.best);
}

// Draw bricks on canvas
function drawBricks() {
  bricks.forEach((column) => {
    column.forEach((brick) => {
      ctx.beginPath();
      ctx.rect(brick.x, brick.y, brick.w, brick.h);
      ctx.fillStyle = brick.visible ? '#0095dd' : 'transparent';
      ctx.fill();
      ctx.closePath();
    });
  });
}

// Move paddle on canvas
function movePaddle() {
  paddle.x += paddle.dx;

  // Wall detection
  if (paddle.x + paddle.w > canvas.width) {
    paddle.x = canvas.width - paddle.w;
  }

  if (paddle.x < 0) {
    paddle.x = 0;
  }
}

// Move ball on canvas
function moveBall() {
  ball.x += ball.dx;
  ball.y += ball.dy;

  // Wall collision (right/left)
  if (ball.x + ball.size > canvas.width || ball.x - ball.size < 0) {
    ball.dx *= -1; // ball.dx = ball.dx * -1
  }

  // Wall collision (top/bottom)
  if (ball.y + ball.size > canvas.height || ball.y - ball.size < 0) {
    ball.dy *= -1;
  }

  // console.log(ball.x, ball.y);

  // Paddle collision
  if (
    ball.x - ball.size > paddle.x &&
    ball.x + ball.size < paddle.x + paddle.w &&
    ball.y + ball.size > paddle.y
  ) {
    ball.dy = -ball.speed;
  }

  // Brick collision
  bricks.forEach((column) => {
    column.forEach((brick) => {
      if (brick.visible) {
        if (
          ball.x - ball.size > brick.x && // left brick side check
          ball.x + ball.size < brick.x + brick.w && // right brick side check
          ball.y + ball.size > brick.y && // top brick side check
          ball.y - ball.size < brick.y + brick.h // bottom brick side check
        ) {
          ball.dy *= -1;
          brick.visible = false;

          increaseScore();
        }
      }
    });
  });

  // Hit bottom wall - Lose
  if (ball.y + ball.size > canvas.height) {
    scoreBoard.score = 0;
    scoreBoard.level = 1;
    paddleWidth = 80;
    paddle.w = paddleWidth;
    speed = 0;
    out = true;
    showAllBricks();
  }
}

// Increase score
function increaseScore() {
  scoreBoard.score++;

  if (scoreBoard.score % (brickRowCount * brickColumnCount) === 0) {
    ball.visible = false;
    paddle.visible = false;
    alert('Congratulations, Youâ€™ve Reached the Next Level');
    speed += 0.5;
    scoreBoard.level += 1;
    paddleWidth += 30;
    setTimeout(function () {
      showAllBricks();
      // score = 0;
      paddle.x = canvas.width / 2 - 40;
      paddle.y = canvas.height - 20;
      paddle.w = paddleWidth;
      ball.x = canvas.width / 2;
      ball.y = canvas.height / 2;
      ball.visible = true;
      paddle.visible = true;
    }, delay);
  }
}

// Make all bricks appear
function showAllBricks() {
  bricks.forEach((column) => {
    column.forEach((brick) => (brick.visible = true));
  });
}

// Draw everything
function draw() {
  // clear canvas
  ctx.clearRect(0, 0, canvas.width, canvas.height);

  drawBall();
  drawPaddle();
  drawScore();
  drawBricks();
}

// Update canvas drawing and animation
function update(canvasRef) {
  movePaddle();
  moveBall();

  // Draw everything
  draw();

  // game lost
  if (out) {
    out = false;
    drawBall('#FF6347'); // change ball color
    setTimeout(() => {
      requestAnimationFrame(update);
    }, delay);
  } else requestAnimationFrame(update);
}
// Keydown event
function keyDown(e) {
  if (e.key === 'Right' || e.key === 'ArrowRight') {
    paddle.dx = paddle.speed;
  } else if (e.key === 'Left' || e.key === 'ArrowLeft') {
    paddle.dx = -paddle.speed;
  }
}

// Keyup event
function keyUp(e) {
  if (
    e.key === 'Right' ||
    e.key === 'ArrowRight' ||
    e.key === 'Left' ||
    e.key === 'ArrowLeft'
  ) {
    paddle.dx = 0;
  }
}

function init(canvasRefs) {
  if (!(canvasRefs && canvasRefs.current)) {
    console.log('canvas reference not available');
    return;
  }
  canvas = canvasRefs.current;
  if (!ctx) {
    // Keyboard event handlers
    document.addEventListener('keydown', keyDown);
    document.addEventListener('keyup', keyUp);

    ball.x = canvas.width / 2;
    ball.y = canvas.height / 2;

    paddle.x = canvas.width / 2 - 40;
    paddle.y = canvas.height - 20;
    ctx = canvas.getContext('2d');
  }
  update();
}

const helpers = {
  init,
};

export default helpers;
